@import url(https://fonts.googleapis.com/css?family=Comfortaa|Exo+2|Raleway|Ubuntu);
/* Color Variables */

:root {
    --one: #e24e42;
    --two: #D9B310;
    --three: #EB6E80;
    --four: #008F95;
    --five: #2B2A2A;
}

/* Media queries */

@media only screen and (max-width: 360px) {
    .trends { 
        padding: 10px;
    }
    
    .ranked-langs {
        padding-bottom: 1rem!important;
    }
}

/* Fonts and Imports */
body {
    background-color: rgb(255, 255, 255);
    background-image: url(/coding_challenge-13/static/media/parallax.4cbbd78c.svg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}


body::-webkit-scrollbar {
    width: 15px;
}

body::-webkit-scrollbar-track {
    /* -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
    -webkit-box-shadow: inset 0 0 6px rgba(231, 47, 1, 0.651);
}

body::-webkit-scrollbar-thumb {
    background-color: #e24e42;
    background-color: var(--one);
    outline: 1px solid #e24e42;
    outline: 1px solid var(--one);
}
.collapse.show {
    background: rgba(255, 255, 255, 0.9);
    margin-top: 17px;
    border-radius: 0px 0px 8px 8px;
}

.title {
    font-family: 'Raleway', sans-serif;
    margin-bottom: 25px;
}

.button-primary {
    text-align: center;
    color: #e24e42;
    color: var(--one);
    padding: 8px 30px;
    border-radius: 15px;
    border: 1px solid #e24e42;
    border: 1px solid var(--one);
    margin: 0 10px;
    background: transparent;
    cursor: pointer !important;
}

.button-primary:focus {
    outline: 0;
}

.button-primary:hover,
.button-primary.active {
    background-color: #EB6E80;
    background-color: var(--three);
    border-color: #EB6E80;
    border-color: var(--three);
    color: white;
    text-decoration: none;
}

.trends {
    width: 80vw;
    margin: 30px auto;
    padding: 50px 30px;
    text-align: center;
    background: #f0d6c886;
    border: 1px solid #fff;
    border-radius: 30px
}

.chart-container {
    position: relative;
    width: 95%;
    margin: 0 auto auto auto;
}

h2 {
    color: #e24e42;
    color: var(--one);
    text-decoration: underline;
}

.svg-flip {
    position: relative;
    top: -1px;
    -webkit-transform: rotate(180deg);
    /* Chrome and other webkit browsers */
    /* FF */
    /* Opera */
    /* IE9 */
    transform: rotate(180deg);
    z-index: -1;
}

.nav-buttons {
    margin-left: auto;
    padding: 15px;
}

.button-primary {
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}

.navbar-toggler {
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}

.navbar-toggler:focus {
    outline:0;
}

.navbar-toggler:hover {
    background-color: var(--three);
}

.navbar-toggler:hover .toggle-button-line {
    background: #fff;
}

.toggle-button-line {
	width: 30px;
    height: 1.5px;
    margin: 5px;
	background: grey;
}

#header {
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}

#header a {
    color: var(--three);
}

#header .active {
    color: #fff;
}

#header a:hover {
    color: #fff
}

.logo-active {
    -webkit-transition: height 0.3s;
    transition: height 0.3s;
    cursor: pointer;
}

.scroll {
    background: rgba(255, 255, 255, 0.9);
    box-shadow: 0 2px var(--three);
    height: 65px;
}

.smLogo>a>img {
    height: 40px;
    position: relative;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.scroll>.nav-buttons {
    position: relative;
}

@media only screen and (max-width: 991px) {
    #header {
