.zone {
    position:relative;
    text-align: center;
    font-size:10em;
    border-radius:4px;
    border:1px solid #bbb;
    background: #A88BEB;
    box-sizing: border-box;
    /* Old browsers */
    /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #A88BEB), color-stop(44%, #F1A7F1), color-stop(100%, #F8CEEC));
    /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(to bottom, #A88BEB 0%, #F1A7F1 44%, #F8CEEC 100%);
    /* Chrome10+,Safari5.1+ */
    /* Opera 11.10+ */
    /* IE10+ */
    background: -webkit-linear-gradient(top, #A88BEB 0%, #F1A7F1 44%, #F8CEEC 100%);
    background: linear-gradient(to bottom, #A88BEB 0%, #F1A7F1 44%, #F8CEEC 100%);
}

.zone > img {
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    max-width: 100%;
    height: auto;
    padding: 5%;
    margin-left: auto;
    margin-right: auto;
}

.more {
    font-size: 4rem;
    color: #fff;
    font-family: Helvetica;
}

.arrow2 {
    display: none;
}

.source-chart {
    margin-top: 40px;
}

